.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

    /* Animation */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__contents {
  display: flex;
  justify-content: space-between;
}

.nav__black {
    background-color: #111;
}

.nav__logo {
  position: fixed;
  left: 0;
  top: 10;
  width: 90px;
  padding-left: 20px;
  cursor: pointer;
}

.nav_avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
