.banner {
  height: 448px;
  position: relative;
  object-fit: contain;
  color: white;
}

.banner__contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner__description {
  width: 45rem;
  line-height: 1.4;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}

@media(max-width:768px){
  .banner__description {
    width: 45rem;
    line-height: 1.4;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 300px;
    height: 80px;
  }
}

.banner--fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}

.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(53, 53, 53, 0.6);
  padding-bottom: 0.5rem;
}

.banner__button:hover {
  color: #000;
  background-color: #e6e6e6e6;
  transition: all 0.2s;
}

